#dark {
    color: var(--color-dark);    
    h1, h2, h3, h4, h5, h6 {
        color: var(--color-dark); 
    }
    a:not(.btn) {
        color: var(--color-dark);  
        &:hover {
            color: #ffffff; 
        }  
    } 
    .header-logo .logo-site svg path {
        @media (min-width: 992px) {
            fill: #ffffff; 
        }
    }
    .logo-footer svg path { 
        fill: #ffffff;  
    }
    .req {
        color: var(--color-dark);
    }
    select,
    textarea, 
    input[type=color], 
    input[type=date], 
    input[type=datetime-local], 
    input[type=datetime], 
    input[type=email], 
    input[type=month], 
    input[type=number], 
    input[type=password], 
    input[type=search], 
    input[type=tel], 
    input[type=text], 
    input[type=time], 
    input[type=url], 
    input[type=week] {
        background-color: #212121; 
        border-color: #212121; 
        color: var(--color-dark);
    }
    #page-body {
        background-color: var(--bg-dark);  
    }
    .section-separator {
        background-color: var(--bg-dark) !important;  
    }
    .title-wrapper {
        border-color: #2f3336;
        >a svg path {
            fill: var(--color-dark);  
        }
    }
    .bw-color {
        background-color: var(--bg-dark);
    }
    .breadcrumb {
        > li.active, > li > a:hover {
            color: #ffffff; 
        }
        > li + li {
            >a {
                background-color: #212121; 
            }
            &:before {
                background-color: #ffffff;  
            }
        }
    }
    .price {
        color: #1da1f2;
    }  
    .price-sale {
        color: #e0245e;
    }
    .price-compare {
        color: var(--color-dark);
    } 
    .quantity {
        border-color: #212121;  
        input {
            background-color: transparent;
        }
        .qty-inner>span {
            background-color: #212121; 
            svg path {
                fill: var(--color-dark);    
            }
        }
    }
    .is-countdown .countdown-section .countdown-amount {
        background-color: transparent;
        color: var(--color-dark); 
    }  
    .top-bar {
        background-color: var(--bg-dark); 
        border-bottom-color: rgba(#d9d9d9, 0.1); 
        .table-row .list-inline > li {
            color: var(--color-dark); 
            &:before {
                background-color: #6e767d;   
            }   
            > a {
                color: var(--color-dark);  
                svg path {
                    fill: var(--color-dark); 
                } 
            }
            .customer-or {
                color: var(--color-dark);
            } 
        }
        &.phone svg path, &.email svg path {
            fill: var(--color-dark);   
        } 
    }
    .mobile-department ul li a {
        color: var(--color-dark);    
    }
    .header-content {
        @media (min-width: 992px) {
            background-color: var(--bg-dark);  
        }
        .header-container.header-fixed:not(.style-5):not(.style-6) .header-main {
            @media (min-width: 992px) {
                background-color: var(--bg-dark);     
            }     
        } 
        .style-2 {
            .table-row .header-contact-box .info span {
                color: var(--color-dark); 
            }
            .header-icons > ul > li {
                >a svg {
                    fill: #181818 !important;   
                }
                .cart-target > .basket > svg path {
                    fill: #181818;
                }
                &.top-cart-holder > .cart-target > .basket {
                    color: #181818;  
                } 
            }
            .header-navigation .searchbox .navbar-form {
                border-color: #212121; 
            }
        }
        .style-3 .header-navigation .sidemenu-holder .navbar .navbar-collapse .menu-list > li {
            >.dropdown-inner > a, > a {
                color: #333e48; 
            }
            >.dropdown-inner .expand svg path {
                fill: #333e48;  
            }
        }
        .style-4 .table-row .header-logo .vertical-menu .sidemenu-holder .navigation .head {
            color: var(--color-dark);
            &:after {
                background-color: var(--color-dark);
            }  
        }
        .style-5 {
            .header-logo .logo-site svg path {
                @media (min-width: 992px) {
                    fill: #181818;  
                }
            }
            .searchbox .navbar-form {
                border-color: #212121;
            }
            .header-icons > ul > li {
                >a svg {
                    fill: #181818 !important;   
                }
                .cart-target>.basket>svg path {
                    fill: #181818;
                }
                &.top-cart-holder > .cart-target > .basket {
                    color: #181818;  
                } 
            }
            .header-navigation {
                border-bottom-color: #212121;
                .navigation .head {
                    color: var(--color-dark);
                    &:after {
                        background: var(--color-dark); 
                    }
                }
            }
        }
        .style-6 {
            .header-logo .logo-site svg path {
                @media (min-width: 992px) {
                    fill: #181818;  
                } 
            }  
            .header-icons > ul > li {
                >a svg {
                    fill: #181818 !important;    
                }
                .cart-target > .basket > svg path {
                    fill: #181818;
                }
                &.top-cart-holder > .cart-target > .basket {
                    color: #181818;  
                } 
            }
            .header-main .searchbox .navbar-form {
                background-color: transparent;
                border: none; 
                .form-control {
                    border-bottom: 1px solid #333e48;
                    color: #333e48;  
                } 
            }
        }
        .style-7 {
            border-bottom-color: #212121;  
            .header-main .vertical-menu .sidemenu-holder .navigation .head svg path {
                fill: var(--color-dark);   
            }
        }
        .style-8 .header-main {
            .off-canvas-navigation-wrapper .navbar-toggle-hamburger svg path {
                fill: var(--color-dark);   
            }
            .shipping-text > span:not(:empty) {
                color: var(--color-dark);    
            } 
        }
        .searchbox {
            &.searchbox-mobile {
                background-color: var(--bg-dark);  
                box-shadow: 0 0 8px rgba(72, 72, 72, 0.75); 
            }  
            .form-control {
                background-color: transparent;
                border-color: transparent; 
            }      
            .navbar-form {
                background-color: var(--bg-dark);   
            } 
            .result-ajax-search ul {
                background-color: var(--bg-dark);  
                li {
                    p {
                        color: var(--color-dark); 
                    }
                    &:hover {
                        background-color: #212121;
                    }
                }
            }
            .search-icon svg path {
                @media (max-width: 991px) {
                    fill: var(--color-dark);  
                } 
            } 
        }  
    }   
    .header-main .m-cart-icon .mobile-basket {
        @media (max-width: 991px) {
            color: #000000; 
        }
    }
    .header-icons > ul > li {
        > a svg {
            fill: var(--color-dark) !important;
        }
        .cart-target > .basket {
            color: var(--color-dark);
            > svg path {
                fill: var(--color-dark);
            }
        }
    }
    .horizontal-menu .sidemenu-holder .menu-list > li {
        &.highlight, &.hover.highlight {
            > .dropdown-inner > a {
                color: #ee0000;
            }  
            > .dropdown-inner >.expand, > a, >.expand {
                svg path {
                    fill: #ee0000; 
                } 
            }
        }
    }  
    .left-column-container, .vertical-menu {
        .sb-vertical-menu, .sidemenu-holder {
            .navbar .navbar-collapse .main-nav {
                background-color: var(--bg-dark);  
                > li {
                    border-color: #212121;   
                    &.mega-is-hover {
                        background-color: #212121; 
                    }
                    &.dropdown > .dropdown-menu > li > a {
                        color: var(--color-dark);
                        span {
                            color: var(--color-dark); 
                        } 
                    }
                    > .dropdown-inner > span.expand svg path {
                        fill: var(--color-dark); 
                    }
                }
            } 
        }
    }
    .sidemenu-holder, .vertical-navbar {
        .menu-list {
            .dropdown-inner, > li {
                > span.expand svg path {
                    fill: var(--color-dark);
                }
            }
            > li {
                &.dropdown > .dropdown-menu > li {
                    > a span {
                        color: var(--color-dark); 
                    }
                    .dropdown-menu > li > a span{
                        color: var(--color-dark);
                    } 
                }
                &.mega-menu {
                    .mm-tab-col-title {
                        border-inline-end-color: #212121;
                        .tab-title > li {
                            color: var(--color-dark);
                            svg path {
                                fill: var(--color-dark);
                            }
                            &.active {
                                color: #ffffff;
                                background-color: #212121;  
                                svg path {
                                    fill: #ffffff;
                                }
                            }
                        }
                    }
                    >.dropdown-menu .mega-sub-link .dropdown-menu-sub li.mega-is-hover {
                        background-color: transparent;    
                    } 
                } 
            }
        }
        .dropdown-menu > li.mega-is-hover {
            background-color: #212121;
        }
    }
    .dropdown-menu {
        background-color: var(--bg-dark) !important;
        border-color: #212121; 
        .dropdown-menu-sub {
            background-color: transparent !important; 
        } 
    }
    .header-navigation .table-row .wrap-horizontal-menu .shipping-text {
        @media (min-width: 1200px) {
            color: var(--color-dark);
        } 
    }
    .menu-mobile {
        background-color: var(--bg-dark); 
        .main-nav {
            span.expand svg path {
                fill: var(--color-dark);
            } 
            li:not(:last-child) {
                border-bottom-color: #212121;
            }
            >li:not(.mobile-layout-bar):hover {
                @media (max-width: 991px) {
                    background-color: #212121;
                } 
            }
            .back-prev-menu {
                > a svg path, > span.back svg path {
                    fill: #000000;
                } 
            }
            .dropdown > .dropdown-menu {
                background-color: var(--bg-dark) !important;  
            }
            .mega-menu .dropdown-menu .mega-sub-link:not(:last-child) {
                border-bottom-color: #212121;  
            } 
        }
        .mobile-contact-bar li a {
            color: #000000; 
        }
    }
    .owl-carousel .owl-nav button span svg path {
        fill: var(--color-dark);  
    }
    .quick_shop:not(:hover) {
        color: var(--color-dark); 
    } 
    .modal-content {
        background-color: var(--bg-dark);  
    }
    #quick-shop-popup .modal-dialog .modal-content .modal-body .product-info {
        #qs-product-title a, 
        .quantity-product label, 
        #qs-description,
        .variants-wrapper label,
        .variants-wrapper .selector-wrapper select {
            color: var(--color-dark);   
        }  
        input {
            background-color: transparent;
        }  
        #qs-product-price {
            .price {
                color: #1da1f2;
            }
            .price-compare {
                color: var(--color-dark);
            }
        }
    } 
    #language-popup .modal-body {
        .language-inner.source-2 .currency-wrapper .currency_wrapper,
        .translate-position .disclosure .disclosure_wrapper {
            border-color: #212121;
            color: var(--color-dark);
            .expand svg path {
                fill: var(--color-dark);
            } 
        } 
        ul {
            background-color: var(--bg-dark); 
            border-color: #212121;
            li {
                >span, a {
                    color: var(--color-dark);
                } 
            }
        } 
    }
    #layer-addcart-modal {
        .addcart-modal-box {
            background-color: var(--bg-dark); 
        } 
        .addcart-modal-product .addcart-modal-info .addcart-modal-price strong {
            color: var(--color-dark); 
        }
        .addcart-modal-action {
            border-color: #212121; 
            .addcart-modal-subtotal strong {
                color: var(--color-dark);   
            }
        }
    }
    .bc-home-slideshow .nav-tab-wrapper .nav-item a {
        color: #333e48; 
    } 
    .product-wrapper {
        background-color: var(--bg-dark); 
        &:before {
            background-color: #212121;
        }
        &:after {
            background-color: #212121; 
            border-color: #212121;  
            box-shadow: none; 
        }
        .product-head .product-des-list {
            color: var(--color-dark);
        } 
        @media (min-width: 1200px) {
            .product-content {
                .price-cart-wrapper .btn-add-cart:not(:hover) {
                    background-color: #495057;
                    border-color: #495057;  
                } 
                .product-button {
                    background-color: #212121;  
                    border-color: #212121; 
                } 
            }
            &:hover .product-content .pc-inner .price-cart-wrapper {
                border-color: #2f3336; 
            }
        }
    }
    .slideshow-02.sidebar-product .bc-product-list {
        border-color: #212121; 
    }
    .nav-tabs {
        border-bottom-color: #212121;  
        &.tab-heading-3 li .nav-link {
            &.active, &.show {
                color: #333e48; 
            }
        }
    }
    #home-left-column + #home-main-content .hp-tabs-section.tab-style-v1 {
        background-color: var(--bg-dark) !important;   
    }  
    .hp-tabs-section {
        background-color: var(--bg-dark) !important; 
        &.heading-false .nav-tab-wrapper .nav {
            border-color: #212121;
        }
        &.tab-style-v1 {
            background-color: #212121 !important; 
            &.no-padding {
                background-color: var(--bg-dark) !important; 
            }  
        }  
        .hp-tabs-special-product .product-wrapper {
            .deal-progress {
                .progress {
                    background-color: #212121; 
                }   
                .deal-stock {
                    .stock-available, .stock-sold {
                        color: var(--color-dark);
                    } 
                }
            }
            .special-offer-text {
                color: #000000; 
            }
            .offer-text {
                color: var(--color-dark);
            }
            .is-countdown .countdown-section {
                .countdown-amount { 
                    background-color: #212121; 
                }    
                .countdown-period {
                    color: var(--color-dark);
                }
                &:after {
                    color: var(--color-dark); 
                }
            }
        }
        &.tab-style-listing .nav-tab-wrapper .nav li a {
            border-bottom-color: #212121; 
        } 
        .col-link svg path {
            fill: var(--color-dark);  
        }
    }
    .bc-wrapper {
        background-color: var(--bg-dark) !important;   
    }
    .product-grid-section {
        .product-grid-content {
            background-color: var(--bg-dark) !important;   
        } 
        .title-wrapper .header-countdown .is-countdown .countdown-section .countdown-amount {
            color: #333e48; 
        }
    }    
    .single-product-section {
        .product-wrapper .deal-progress {
            .stock-sold, .stock-available {
                color: var(--color-dark); 
            }
            .progress {
                background-color: #212121;  
            } 
        } 
        .style-1 .sp-item .product-wrapper {
            background-color: transparent; 
            .product-name a {
                color: #333e48; 
            } 
            .deal-progress {
                .stock-available, .stock-sold {
                    color: #333e48;
                }
                .progress {
                    background-color: #eeeeee; 
                } 
            }
            .price-compare {
                color: #333e48; 
            }
            .is-countdown .countdown-section {
                .countdown-amount, .countdown-period {
                    color: #333e48;
                }
            }
        }
    } 
    .home-banner-v2 {
        background-color: var(--bg-dark) !important; 
    }  
    .menu-image-section {
        .menu-image-content {
            border-color: #2f3336;
        }
        .box-menu {
            background-color: #212121; 
        } 
        .nav-tab-wrapper {
            background-color: var(--bg-dark);  
            .nav-tabs li {
                border-color: #2f3336; 
            }
        }
        .tab-content {
            @media (min-width: 992px) {
                border-color: #212121; 
            }    
        }
    }
    .bottom-partner-list, .widget-partner {
        border-color: #212121; 
    }
    .product-listing-items .listing-items-col>h4, .product-listing-section .listing-items-col>h4 {
        border-color: #212121; 
    } 
    .product-listing-section .listing-items-col .listing-item .product-content {
        .price {
            color: #1da1f2;
        }
        .price-sale {
            color: #e0245e; 
        }
    }
    .footer-widget {
        background-color: var(--bg-dark);  
    }
    .footer-logo {
        .support-box-1 .text {
            color: var(--color-dark);
        }
        .support-box-2 .text {
            color: var(--color-dark);
        }
    }
    .footer-bot { 
        background-color: #212121; 
    }
    .copyright p {
        color: var(--color-dark); 
    } 
    .widget-social ul li a svg path {
        fill: var(--color-dark);
    } 
    .category {
        border-bottom-color: #212121;   
        &:hover {
            box-shadow: none; 
        }
        &:before {
            background-color: #212121;
        } 
    }
    .sb-widget {
        h5 {
            border-color: #212121;
        }
        .sb-menu {
            border-color: #212121;
        }
        .blog-menu, .categories-menu {
            li .dropdown-menu {
                border-color: #212121; 
            }
            >li {
                border-color: #212121; 
            }
            .expand svg path {
                fill: var(--color-dark); 
            }
        }
        &.style-accordion .sbf-title:after {
            background-color: var(--color-dark);  
        }
        &.filter-sidebar .sbw-filter {
            border-color: #212121; 
        }
        .latest-product-item .listing-item {
            border-bottom-color: #212121;
        }
        .sb-blog-tag ul li a {
            border-color: #212121;
        }
        .sb-service {
            border-color: #212121;  
            .sb-service-item {
                border-color: #212121; 
                .list-si li {
                    color: var(--color-dark); 
                } 
            }   
        } 
    }
    .sbw-filter > div {
        .sbf-title {
            color: var(--color-dark);
        } 
    }
    .filter-sidebar.position-body.style-drawer {
        background-color: var(--bg-dark); 
    }
    .cata-toolbar {
        background-color: #212121;   
        .grid-list .grid {
            svg path {
                fill: var(--color-dark);
            }
            &:hover, &.active  {
                svg path {
                    fill: #ffffff;
                } 
            }
        }
        .sort-by {
            color: var(--color-dark);
            svg path {
                fill: var(--color-dark);  
            } 
            .sort-by-inner {
                background-color: var(--bg-dark); 
                border-color: #212121; 
                #cata_sort_by button a, label {
                    color: var(--color-dark); 
                } 
            } 
            #sort_by_box {
                background-color: var(--bg-dark); 
                border-color: #212121;
                li {
                    a {
                        color: var(--color-dark); 
                    }
                    &.active {   
                        background-color: rgba(#212121, 0.2);
                        a {
                            color: #ffffff;
                        } 
                    }
                    &:hover {
                        @media (min-width: 1025px) {
                            background-color: rgba(#212121, 0.2); 
                        }
                    }
                }
            }
        }
    }  
    .pagination-holder .pagination {
        border-color: #212121;
        >li>a, >li>span {
            border-color: #212121;
        }
    }
    .purchase-wrapper .envato-purchase-popup {
        background-color: var(--bg-dark);
        box-shadow: none;
        &:after {
            background-color: var(--bg-dark);  
            box-shadow: 1px 1px 4px #3c3c3c;  
        }
        .popup-top {
            box-shadow: 0 2px 4px #3c3c3c;
        }
    }
    .page-cata .subcategory {
        border-bottom-color: #212121;
    } 
    .page-product {
        .product-sku-collection li>span {
            color: var(--color-dark);
        }
        .product-content-wrapper {
            .short-description {
                color: var(--color-dark);
                border-bottom-color: #212121;
            }
            .secure-image {
                border-top-color: #212121; 
            } 
            .group-cw .swatch {
                .header, .swatch-element label {
                    color: var(--color-dark); 
                } 
            } 
            .product-action {
                .quantity-product label, .variants-wrapper label {
                    color: var(--color-dark);  
                }
            }
            .detail-deal .wrapper-countdown > div {
                .countdown-period {
                    color: var(--color-dark); 
                }
                .countdown-amount {
                    color: var(--color-dark); 
                }
            }
            #stock {
                border-color: #212121;  
                span {
                    color: var(--color-dark);
                }
            }  
        }
        .people-in-cart .people-block-text {
            color: var(--color-dark);  
        }
        .shipping-time {
            .delivery-time, li {
                color: var(--color-dark); 
            } 
        }
        .slider-thumbs-03 .slick-list .slick-item {
            border-color: #212121; 
        } 
        &.layout-extended {
            .product-info-inner {
                border-color: #212121;
            }
            .product-simple-tab {
                .container > div[role=tabpanel] {
                    background-color: var(--bg-dark); 
                }
                .nav {
                    &:before {
                        background-color: #212121;
                    } 
                    li:before {
                        background-color: #212121; 
                    }
                }
            } 
        }
        .share-links.social-sharing {
            border-bottom-color: #212121;
        } 
    }
    .product-simple-tab {
        background-color: var(--bg-dark) !important;   
    }
    .banner-wrapper .container .banner-text h3 {
        color: #000000; 
    }
    .type-our-team {
        background-color: var(--bg-dark) !important;  
        .wrap-item .item .name {
            color: var(--color-dark); 
        } 
    } 
    .contact-form .form-group label {
        color: var(--color-dark); 
    } 
    .control-wrapper label {
        color: var(--color-dark); 
    }
    .login-or, .register-or {
        border-inline-start-color: #212121;
        &:before {
            background-color: var(--bg-dark);
            color: var(--color-dark);
            border-color: #212121;
        }
    }
    .login-or:after {
        background-color: #212121;  
    }
    .register-or {
        border-inline-start-color: none; 
        border-inline-end-color: #212121;
    }
    #recover-password, #register-form, .page-login, .page-register {
        .heading {
            border-color: #212121;
        }
    }
    .list-benefits > li {
        color: var(--color-dark);
    } 
    .page-account h2 {
        border-bottom-color: #212121;
    }
    .page-address .address-table {
        border-top-color: #212121;
    }
    .page-content {
        ul li, ol li {
            color: var(--color-dark);
        } 
    }
    .page-faq {
        .faq-ship-title {
            border-bottom-color: #212121;
        }
        .faq-accordion {
            .panel-title svg path {
                fill: var(--color-dark);
            }
            .panel-faq {
                border-bottom-color: #212121;
            }
            .panel-collapse {
                color: var(--color-dark);
            }
        }
    }
    .search-blog form button svg path {
        fill: var(--color-dark);
    }
    .post-item .post-item-inner .post-info {
        color: var(--color-dark);
        border-bottom-color: #212121;
    }
    .post .post-content .post-info {
        border-bottom-color: #212121;
    }
    .grid-comments h5 {
        border-bottom-color: #212121;
    }
    .post-author-info {
        background-color: #212121; 
        .media .text .author {
            color: var(--color-dark);  
        }
    }  
    .comment-form h3 {
        border-bottom-color: #212121;
    }
    .cart-dd, .cart-sb {
        background-color: var(--bg-dark);
        #cart-info {
            .items-inner {
                border-bottom-color: #212121;
                .cart-item-price {
                    color: var(--color-dark);
                }
                .cart-item-quantity span {
                    color: var(--color-dark);
                }  
                .cart-close svg path {
                    fill: var(--color-dark);  
                } 
            }
            .subtotal {
                border-bottom-color: #212121;
                border-top-color: #212121;
                > span.cart-item-total-price {
                    color: var(--color-dark); 
                }
            }
        }
    }
    .cart-dd {
        border-color: #212121; 
    }
    .cart-sb {
        .wrap-action {
            background-color: var(--bg-dark);
        } 
    }
    table {
        tbody tr.last td {
            border-color: #212121;
        } 
        thead th {
            color: var(--color-dark); 
            border-bottom-color: #212121; 
            &.price {
                color: var(--color-dark); 
            }  
        }
        td {
            &.item-price span, &.item-total, &.product-price span {
                color: var(--color-dark) !important; 
            }
        }
    }
    .page-cart .quantity {
        background-color: #212121;
        border-color: #212121;
    } 
    .cart-right .total-price {
        color: var(--color-dark);  
        border-bottom-color: #212121;
        >span:last-child {
            color: var(--color-dark);  
        }
    }
    .cart-table table {
        @media (max-width: 575px) {
            td.item-qty .mobile-remove-action .cart-remove-btn {
                color: #000000; 
            }
            .cart-item {
                border-color: #212121;
            } 
        }  
    } 
    #discount-modal .wrap-modal {
        background-color: var(--bg-dark);
    } 
    .list-collection-section {
        background-color: var(--bg-dark) !important; 
        .item {
            background-color: var(--bg-dark);
        } 
    }
    .list-collection {
        &.border-true .item {
            border-inline-start-color: #212121; 
        } 
        .item:hover {
            @media (min-width: 1025px) {
                box-shadow: 0 0 5px 0 #272727;
            } 
        }
    }
    .policies-content {
        border-color: #212121; 
        .policy-item {
            border-inline-start-color: #212121;
            .text span {
                color: var(--color-dark); 
            } 
        }
    }
    .slideshow-catalog-wrapper.sidebar-banner .bc-three-banner .bc-item-inner .bc-item-body {
        .bc-item-text a {
            color: #333e48;   
        }
        > a {
            color: #333e48;
        }
    }
    .footer-block {
        &.footer-menu {
            @media (max-width: 575px) {
                background-color: #212121;  
            } 
        }
        >h6 .icon svg path {
            fill: var(--color-dark);
        }
    }
    .slideshow-02 .block-collection > .collection-item .category .category-name {
        color: #333e48;
    }
    .popular-search-wrapper ul > li {
        background-color: #212121;  
    }
    .section-page-store-location {
        .map-search, .store-list {
            background: #212121;
        } 
        .store-item svg path {
            fill: var(--color-dark); 
        }
        .map-search_item input, .select-group select {
            background-color: var(--bg-dark);  
        }
    }
} 
.templateProduct #dark {
    .tab-content {
        border-color: #212121;
        .bundled-items {
            .bundled-images li:not(:first-child) { 
                &:after {
                    background-color: #212121;  
                }   
                &:before {
                    background-color: #212121;
                    border-color: #212121; 
                    color: var(--color-dark);  
                }     
            }  
            .bundled-item:first-child .bundled-product-title { 
                color: var(--color-dark);    
            } 
        }
    }
}
.templateListCollections #dark {
    .bottom-container, #footer-content {
        background-color: var(--bg-dark);     
    }  
}
.templateCollection #dark {
    .left-column-container {
        @media (max-width: 991px) {
            background-color: var(--bg-dark);
        }
    }
}
.templateCart #dark .mobile-total-price {
    .total-price {
        color: var(--color-dark);
        border-top-color: #212121; 
    } 
    .mobile-cart-action {
        border-bottom-color: #212121; 
    } 
    &.header-mobile-fixed {
        background-color: var(--bg-dark);
    }  
}  
.cata-grid-1 #dark, .cata-grid-2 #dark {
    .cata-product .product-grid-item {
        border-bottom-color: #212121;
        .product-wrapper {
            .product-button {
                background-color: transparent;
                border-color: transparent; 
            } 
            .price-cart-wrapper {
                border-bottom-color: #212121;
            }
        }
    } 
}